/**  =====================
      Custom css start
==========================  **/
body {
    font-family: $theme-font-family;
    font-size: $theme-font-size;
    color: $theme-font-color;
    font-weight: 400;
    background: $theme-background;
    position: relative;

}


* {
    &:focus {
        outline: none;
    }
}

a {
    text-decoration: none;
    &:hover {
        cursor:pointer;
        outline: none;
        text-decoration: none;
    }
}

.btn-primary {
color: #291C60 !important;
}


h3,
h4,
h5,
h6 {
    font-family: $theme-font-family;
    color: $theme-heading-color;
    font-weight: 400;
}

h1 {
    font-family: $theme-font-titles;
    font-weight: 900;
    font-size: 48px;
    &.h1bold{
        font-weight: 800;
    }
}

h2 {
    font-family: $theme-font-titles;
    font-weight: 700;
    font-size: 44px;
}

h3 {
    font-size: 26px;
}

h4 {
    font-size: 20px;
}

h5 {
    font-size: 18px;
}

h6 {
    font-size: 14px;
}

p {
    font-size: 14px;
}

strong {
    font-weight: 400;
}

.img-radius {
    border-radius: 50%;
}
.text-info {
    color: $theme-color2!important;
}

.text-success {
    color: $theme-color!important;
}


.btn-primary {
    &:focus{
    background-color: $default-color;
    border-color: $default-color;
    color:#291C60;

    }
}
.btn-secondary {
    background-color:$cta !important;
    border-color: $cta!important;
    &:disabled {
        background-color:$default-color !important;
        border-color:$default-color !important;
    }
}
.disabled {
    pointer-events:none;
    cursor: not-allowed;
    background: #eee !important;
    color:#AAA;
}

.showHideContainer {
    position:relative;
}
.showHide {
    position: absolute;
    right: 19px;
    top: 39px;
    i {
        font-size:22px;
        color:$secondary-color;
        &:hover{
            opacity:0.5;
        }
    }
}

.page-header-title + .breadcrumb {
    background: transparent;
    padding: 0;

    > .breadcrumb-item {
        a {
            color: #888;
        }

        &:last-child a {
            color: #111;
            font-weight: 600;
        }
    }
}

.table-hover > tbody > tr:hover {
    opacity: 0.95;
}

.Comment {
    border: transparent;
}
.unread.d-flex {
    border-bottom: transparent !important;
}


.input-group-text {
    padding: 0.675rem 0.75rem !important;
}

@media (min-width: 992px) {
    .navbar-expand-lg .navbar-collapse {
      justify-content: space-between !important;
    }
  }

  .float-right {
    float: right;
    margin: 2% 0;
  }

.theme-bg {
    background: $theme-color;
}

.theme-bg2 {
    background: $theme-color2;
}

.close {
    font-size: 3rem;
    font-weight: 400;
    line-height: 1;
    color: $secondary-color;
    text-shadow: 0 1px 0 #fff;
    background: transparent;
  border: none;
}
/* ==========  card css start  =========== */

.anim-rotate {
    animation: anim-rotate 1s linear infinite;
}
@keyframes anim-rotate {
    100% {
        transform: rotate(360deg);
    }
}

.anim-close-card {
    animation: anim-close-card 1.4s linear;
}
@keyframes anim-close-card {
    100% {
        opacity: 0.3;
        transform: scale3d(.3, .3, .3);
    }
}

p.text-muted {
    font-size: 13px;
}

.card {
    border-radius: 0;
    box-shadow: 0 1px 20px 0 rgba(69, 90, 100, 0.08);
    border: none;
    margin-bottom: 30px;
    transition: all 0.5s ease-in-out;
    .collapse {
        .dashboard_cardBodyPadding {
            .card-body {
                padding:1%;

                h2{
                    font-size:36px;
                }
                p{
                    font-size:12px;
                    margin-bottom:0.3rem;
                    color:$menu-light-text-color;
                }
            }
        }
    }
    &.monitor_error {
        .card-header {
            h5 {
                color:$warning;
                &:after {
                    content: "";
                    width: 4px;
                    background-color: $warning;
                    animation: blink ease-in-out 1500ms infinite;
                }
            }
        }


    }
    &.monitor_success {
        .card-header {
            h5 {
                color:$primary-color;
                &:after {
                    content: "";
                    width: 4px;
                    background-color: $primary-color;

                }
            }
        }
    }
    &.monitor_notdeployed {
        .card-header {
            h5 {
                color:$secondary-color;
                &:after {
                    content: "";
                    width: 4px;
                    background-color:$secondary-color;

                }
            }
        }

    }
    .card-header {
        background-color: transparent;
        border-bottom: 1px solid #f1f1f1;
        padding: 20px 25px;
        position: relative;

        + .card-block,
        + .card-body {
        }

        h5 {
            margin-bottom: 0;
            color: #000;
            font-size: 17px;
            font-weight: 400;
            display: inline-block;
            margin-right: 10px;
            line-height: 1.1;
            position: relative;

            &:after {
                content: "";
                background-color: $primary-color;
                position: absolute;
                left: -25px;
                top: 0;
                width: 4px;
                height: 20px;
            }
        }

        &.borderless {
            border-bottom: none;

            h5:after {
                display: none;
            }
        }

        .card-header-right {
            right: 10px;
            top: 10px;
            display: inline-block;
            float: right;
            padding: 0;
            position: absolute;
            @media only screen and (max-width: 575px) {
                display: none;
            }

            .dropdown-menu {
                margin-top: 0;

                li {
                    a {
                        font-size: 14px;
                        text-transform: capitalize;
                    }
                }
            }

            .btn.dropdown-toggle {
                border: none;
                background: transparent;
                box-shadow: none;
                color: #888;

                i {
                    margin-right: 0;
                }

                &:after {
                    display: none;
                }

                &:focus {
                    box-shadow: none;
                    outline: none;
                }
            }
        }
    }
    .circle {
        margin: 0 auto;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        padding: 0px;
        background: #fff;
        border: 2px solid $warning;
        text-align: center;
        animation: glowing ease-in-out 1500ms infinite;

    }
    @keyframes glowing {
        0% {
          box-shadow: 0px 0px 1px 6px rgba(249, 246, 152, 0.1);
        }
        50% {
            box-shadow: 0px 0px 2px 10px rgba(255, 247, 0, 0.3);
        }
        100% {
            box-shadow: 0px 0px 1px 6px rgba(249, 246, 152, 0.1);
        }
      }
    .card-footer {
        border-top: 1px solid #f1f1f1;
        background: transparent;
        padding: 25px;
    }

    .card-block,
    .load_container,
    .card-body {
        // padding: 30px 25px;
        .card-load {
            position: relative;
            overflow: hidden;

            .card-loader {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                background-color: rgba(256, 256, 256,0.7);
                z-index: 999;

                i {
                    margin: 0 auto;
                    color: $primary-color;
                    font-size: 24px;
                    align-items: center;
                    display: flex;
                }
            }
        }
    }

    &.card-load {
        position: relative;
        overflow: hidden;

        .card-loader {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            background-color: rgba(256, 256, 256,0.7);
            z-index: 999;

            i {
                margin: 0 auto;
                color: $primary-color;
                font-size: 24px;
                align-items: center;
                display: flex;
            }
        }
    }

    &.full-card {
        z-index: 99999;
        border-radius: 0;
    }
}

