/* Example modals */

.modal {
    z-index: 1072;

    .popover,
    .tooltip {
        z-index: 1073;
    }
}

.modal-backdrop {
    z-index: 1071;
    &.show{
        opacity: 0.2;
    }
}

.bd-example-modal {
    background-color: #fafafa;

    .modal {
        position: relative;
        top: auto;
        right: auto;
        bottom: auto;
        left: auto;
        z-index: 1;
        display: block;
    }

    .modal-dialog {
        left: auto;
        margin-right: auto;
        margin-left: auto;

    }
}


.modal.fade .modal-dialog {
    right: -320px;
    -webkit-transition: opacity 0.1s linear, right 0.1s ease-out;
       -moz-transition: opacity 0.1s linear, right 0.1s ease-out;
         -o-transition: opacity 0.1s linear, right 0.1s ease-out;
            transition: opacity 0.1s linear, right 0.1s ease-out;
  }

  .modal.show .modal-dialog {
    right: 0;
    -webkit-transition: opacity 0.1s linear, right 0.1s ease-out;
       -moz-transition: opacity 0.1s linear, right 0.1s ease-out;
         -o-transition: opacity 0.1s linear, right 0.1s ease-out;
            transition: opacity 0.1s linear, right 0.1s ease-out;
  }


