    .card-event i {
        right: auto;
        left: 27px;
    }

    .bitcoin-wallet i {
        right: auto;
        left: 50px;
    }

    .Active-visitor .card-active > div + div,
    .card-social .card-active > div + div {
        border-right: 1px solid $theme-border;
        border-left: 0 !important;
    }

    .br-theme-bars-reversed .br-widget .br-current-rating {
        text-align: left;
    }

    .jstree-default .jstree-node {
        background-position: -320px -4px;
    }

    .sw-theme-dots > ul.step-anchor > li > a:after {
        right: 49%;
    }

    .dtp > .dtp-content {
        right: 50%;
        margin-right: -150px;
    }

    .note-card .Note-delete {
        left: 10px;
    }

    .filter-bar .nav {
        padding-right: 0;
    }

    .footable .pagination {
        padding: 0;
    }

    .h-list-body {
        .chat-messages {
            .chat-menu-content {
                > div {
                    &:before {
                        content: "\67";
                        font-family: "pct";
                        color: #1de9b6;
                        left: auto;
                        text-shadow: 4px 4px 10px rgba(0, 0, 0, 0.15);
                        right: -19px;
                    }
                }

                p {
                    &:first-child {
                        border-top-left-radius: 10px !important;
                        border-top-right-radius: 0 !important;
                    }
                }
            }

            .chat-menu-reply {
                text-align: left;

                > div {
                    &:before {
                        content: "\65";
                        right: auto;
                        left: 5px;
                    }

                    p {
                        &:last-child {
                            border-bottom-left-radius: 0;
                            border-bottom-right-radius: 8px;
                        }
                    }

                    .chat-cont {
                        margin-left: 25px;
                        margin-right: 0;
                    }
                }
            }
        }
    }

    .chat-sanders {
        .send-chat {
            .msg {
                &:after {
                    left: -12px;
                    right: auto;
                    border-bottom-color: transparent;
                    border-top-color: #1dc8e5;
                }
            }
        }

        .received-chat {
            .msg {
                &:after {
                    right: -12px;
                    transform: rotate(90deg);
                    left: auto;
                }
            }
        }
    }

    .form-check {
        padding-left: 0;
        padding-right: 1.25rem;

        input {
            margin-right: -20px;
        }
    }

    .form-check-input {
        margin-right: 0;

        + label {
            margin-right: 1.3rem;
        }
    }

    .bs-tooltip-right .arrow::before {
        left: auto;
        right: -8px;
    }

    .br-theme-bars-1to10 .br-widget .br-current-rating,
    .br-theme-bars-1to10 .br-widget a,
    .br-theme-bars-movie .br-widget a,
    .br-theme-bars-reversed .br-widget .br-current-rating,
    .br-theme-bars-reversed .br-widget a,
    .br-theme-bars-square .br-widget a,
    .br-theme-css-stars .br-widget a {
        float: right;
    }

    .br-theme-bars-reversed .br-widget .br-current-rating {
        text-align: right;
    }

    .tool-container.tool-top .arrow {
        margin-right: -7px;
    }

    .sw-theme-arrows > ul.step-anchor > li:first-child > a {
        padding-right: 15px;
        padding-left: 30px;
    }

    .minicolors .form-control {
        padding: 6px 44px 5px 12px;
    }

    .minicolors-theme-bootstrap .minicolors-swatch {
        right: 3px;
    }

    .minicolors-position-left .minicolors-panel {
        right: 0;
    }

    .select2-container--default .select2-selection--multiple .select2-selection__choice {
        float: right;
    }
    /* =========   Extra pages end ============= */
    @media only screen and (max-width: 768px) {
        .sw-theme-dots {
            > ul.step-anchor {
                &:before {
                    right: 1px;
                    margin-left: 10px;
                    margin-right: 0;
                }

                > li {
                    > a {
                        &:before {
                            right: -23px;
                            margin-right: 10px;
                        }

                        &:after {
                            right: -40px;
                        }
                    }
                }
            }
        }
    }

    .mce-menu.mce-menu-align .mce-caret,
    .mce-menu.mce-menu-align .mce-menu-shortcut {
        left: 0;
        right: auto;
    }

    .mce-menu-item .mce-caret {
        margin-left: 6px;
    }

    .ux-designer .btn {
        right: auto;
        left: 20px;
    }

    .owl-carousel {
        direction: ltr;
    }
    /* ========= mobile menu start ============ */
    @media only screen and (max-width: 991px) {
        .pcoded-header {
            .m-header {
                .mobile-menu {
                    right: auto;
                }

                .b-brand {
                    margin-left: 0;
                    margin-right: 20px;
                }
            }

            .navbar-nav.mr-auto li.nav-item:not(.dropdown) {
                padding-left: 0;
                padding-right: 20px;
            }

            #mobile-header {
                left: 20px;
                right: auto;
            }
        }

        .pcoded-header .ml-auto {
            float: left;
        }
    }
    @media only screen and (max-width: 575px) {
        .pcoded-header {
            .dropdown {
                .dropdown-menu {
                    right: 0;
                    left: 0;
                    margin-left: auto;
                }
            }
        }
    }
    /* ======== mobile menu and ============== */
    /**====== RTl css end ======**/

    .ps--active-x > .ps__rail-x,
    .ps--active-y > .ps__rail-y {
        right: calc(100% - 15px) !important;
    }

    &.layout-1 {
        .pcoded-navbar {
            .side-content {
                .sidelink {
                    .pcoded-inner-navbar {
                        > li {
                            > a {
                                padding: 12px 20px 12px 10px;
                            }
                        }

                        li {
                            &.pcoded-hasmenu {
                                .pcoded-submenu {
                                    li {
                                        > a {
                                            padding: 7px 40px 7px 7px;

                                            &:before {
                                                right: 20px;
                                            }
                                        }

                                        .pcoded-submenu > li > a {
                                            padding: 7px 60px 7px 7px;

                                            &:before {
                                                right: 40px;
                                            }
                                        }
                                    }
                                }
                            }

                            &.pcoded-menu-caption:after {
                                left: auto;
                                right: 18px;
                            }
                        }
                    }
                }
            }

            .sidemenu {
                box-shadow: inset 1px 0 0 0 rgba(169, 183, 208, 0.2);
                right: auto;
                li {
                    &.active:after {
                        right: auto;
                        left: 0;
                    }
                }
            }

            &[class*='navbar-'] {
                .sidemenu {
                    box-shadow: inset 1px 0 0 0 rgba(255, 255, 255, 0.3);
                }
            }
            &.menu-light .sidemenu {
                box-shadow: inset 1px 0 0 0 rgba(169, 183, 208, 0.4)
            }
            .toggle-sidemenu {
                transform: rotate(180deg);
                border-radius: 0 0 5px 0;
                left: auto;
                right: 0;
            }
            &.hide-sidemenu {
                .sidemenu {
                    left: auto;
                    right: -#{$Menu-collapsed-width};
                }
            }
        }
    }
}
